@import url('https://fonts.googleapis.com/css?family=Montserrat:400,700');
@import url('https://maxcdn.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css');

body {
  overflow-y: auto !important;
}

.App {
  color: white;
}

a {
  color: white;
  text-decoration: none;
}

a:visited {
  color: white;
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

.NavLinks {
  padding-left: 8px;
  padding-right: 8px;
}

.NavLinks:hover {
  color: #ffef00;
}

.Videos {
  min-width: 200px;
  height: 180px;
  margin: 3px;
}

.VideoList {
  width: 32%;
  min-width: 320px;
  height: 230px;
  margin: 6px;
  /*	flex-grow: 1; */
}

.Videos:hover {
  -webkit-animation: grow .4s ease forwards;
          animation: grow .4s ease forwards;
  z-index: 100;
}

.video-grid:hover .Videos:not(:hover) {
  -webkit-animation: darken .4s ease forwards;
          animation: darken .4s ease forwards;
  z-index: -99;
}

@-webkit-keyframes darken {
  to {
    opacity: .6;
  }
}

@keyframes darken {
  to {
    opacity: .6;
  }
}

@-webkit-keyframes grow {
  to {
    -webkit-box-shadow: 0px 2px 15px rgba(0, 0, 0, 0.4);
            box-shadow: 0px 2px 15px rgba(0, 0, 0, 0.4);
  }
}

@keyframes grow {
  to {
    -webkit-box-shadow: 0px 2px 15px rgba(0, 0, 0, 0.4);
            box-shadow: 0px 2px 15px rgba(0, 0, 0, 0.4);
  }
}

::-moz-selection {
  background: #ffef00;
  /* WebKit/Blink Browsers */
}

::selection {
  background: #ffef00;
  /* WebKit/Blink Browsers */
}

::-moz-selection {
  background: #ffef00;
  /* Gecko Browsers */
}

.VideoOverlay i {
  font-size: 18px;
  padding-left: 15px;
  padding-right: 15px;
  -webkit-animation: vidLinks .2s ease forwards;
          animation: vidLinks .2s ease forwards;
}

.VideoOverlay a {
  color: white;
  text-decoration: none;
}

.VideoOverlay a:visited {
  color: white;
  text-decoration: none;
}

.VideoOverlay .fa-link {
  -webkit-animation: vidLinks .15s ease forwards;
          animation: vidLinks .15s ease forwards;
}

.VideoOverlay .fa-bookmark {
  -webkit-animation: vidLinks .2s ease forwards;
          animation: vidLinks .2s ease forwards;
}

.VideoOverlay .fa-heart {
  -webkit-animation: vidLinks .25s ease forwards;
          animation: vidLinks .25s ease forwards;
}

.fa-link:hover {
  color: #ffef00;
}

.fa-bookmark:hover {
  color: #8BFF0D;
}

.fa-heart:hover {
  color: #CC1441;
}

.VideoOverlay .fa-times:hover {
  color: #a7a7a7;
}

.growIcon {
  -webkit-animation: growIcon .3s ease;
          animation: growIcon .3s ease;
}

@-webkit-keyframes vidLinks {
  from {
    -webkit-transform: translateY(30px);
            transform: translateY(30px);
  }
  to {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}

@keyframes vidLinks {
  from {
    -webkit-transform: translateY(30px);
            transform: translateY(30px);
  }
  to {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}

@-webkit-keyframes growIcon {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
  }
  30% {
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
    opacity: 5;
  }
  70% {
    -webkit-transform: scale(1.7);
            transform: scale(1.7);
    opacity: 0;
  }
  100% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    opacity: 0;
  }
}

@keyframes growIcon {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
  }
  30% {
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
    opacity: 5;
  }
  70% {
    -webkit-transform: scale(1.7);
            transform: scale(1.7);
    opacity: 0;
  }
  100% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    opacity: 0;
  }
}

.main-container {
  display: none;
}

.loading-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  height: 60%;
}

.loading-circle {
  height: 45px;
  width: 45px;
  background: #ffef00;
  border-radius: 100%;
  -webkit-animation: enlargeCircle 1.6s cubic-bezier(0, 1.64, .59, .68) infinite;
          animation: enlargeCircle 1.6s cubic-bezier(0, 1.64, .59, .68) infinite;
}

@-webkit-keyframes enlargeCircle {
  0% {
    height: 30px;
    width: 30px;
  }
  50% {
    height: 0px;
    width: 0px;
  }
}

@keyframes enlargeCircle {
  0% {
    height: 30px;
    width: 30px;
  }
  50% {
    height: 0px;
    width: 0px;
  }
}

.ScreenAnimate {
  -webkit-animation: slideUp .6s cubic-bezier(0, .99, .41, .88) forwards;
          animation: slideUp .6s cubic-bezier(0, .99, .41, .88) forwards;
}

@-webkit-keyframes slideUp {
  from {
    -webkit-transform: translateY(300px);
            transform: translateY(300px);
  }
  to {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}

@keyframes slideUp {
  from {
    -webkit-transform: translateY(300px);
            transform: translateY(300px);
  }
  to {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}

@media screen and (max-width: 740px) {
  .videoContainer {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
  }
  .VideoList {
    width: 90%;
    min-width: 320px;
    height: 230px;
    margin: 6px;
  }
}


